body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
}

.min-height {
	min-height: 350px;
}

.float-right {
	padding-bottom: 10px;
	float: right;
}

@media print {
	.no-print, .negative.message {
		display: none !important;
	}

	.print, .segment {
		display: block;
		page-break-before: always;
	}
}
.round-buttons {
  background: #0a52c1;
  width: 100%;
  display: block;
  margin: 0;
  padding: 10px;
  border-radius: 15px;
  color: white;
  text-align: center;
}

.round-buttons:hover {
  color: white;
  transition: all ease-out 300ms;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.round-buttons.finished {
  background: #cf0b0d;
}

.startDate {
  display: block;
}

.green-table {
  background: #5aa041;
}

.red-table {
  background: #f75151;
}

.list.similar-height:first-child {
  margin: 1em 0;
}

.finished {
  color: lightgray;
}

.finished .item .content .header {
  color: lightgray;
}

.postponed,
.postponed .item .content .header {
  color: red;
}

.event-hover:hover {
  cursor: pointer;
}

.event {
  margin: 10px 0px;
}

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

.red {
  color: red;
  background: none;
}

.green {
  color: green;
  background: none;
}

.special {
  font-weight: bold;
}

.playerAnchor:hover {
  cursor: pointer;
}

.wide-text {
  width: 100%;
}

.red {
  color: red;
}

.green {
  color: green;
}

.remove-league:hover {
  cursor: pointer;
}

.dropdown {
  margin: 10px 0px;
}

